<template>
  <div class="auth-page">
    <b-container>
      <h5 class="auth-logo">
        <i class="fa fa-circle text-gray"></i>
       Delivery App
        <i class="fa fa-circle text-warning"></i>
      </h5>
      <Widget class="widget-auth mx-auto"  customHeader>
        <p class="widget-auth-info">
            Crie Uma nova conta.
        </p>
        <form class="mt" @submit.prevent="login">
          <b-alert class="alert-sm" variant="danger" :show="!!errorMessage">
            {{errorMessage}}
          </b-alert>
          <div class="form-group">
            <input class="form-control no-border"  placeholder="Nome" />
          </div>
          <div class="form-group">
            <input class="form-control no-border"  placeholder="Email" />
          </div>
          <div class="form-group">
            <input class="form-control no-border"  placeholder="Senha" />
          </div>
          <b-button type="submit" size="sm" class="auth-btn mb-3" variant="inverse" @click="submit()">Registrar</b-button>


       </form>

        <router-link class="d-block text-center" to="login">Esqueceu sua senha?</router-link>
      </Widget>
    </b-container>
    <footer class="auth-footer">
      2019 &copy; Hot delivery App.
    </footer>
  </div>
</template>

<script>
import Widget from '@/components/Widget/Widget';
import Form from '@/store/Form.js';

export default {
  name: 'Register',
  components: { Widget, Form },
  data() {
    return {


        form: new Form({
        company_name: {},

        user_name: {},

        email: {},

        Password: {},

        }),

      errorMessage: null,
    };
  },
  methods: {

    submit() {      
    
        this.registerNewUserRequest()
      
    },

    // make a request
    registerNewUserRequest() {
    
      axios
        .post("registerNewUser", this.form.getValues())
        .then(response => {
          
          this.redirect()
        })
       .catch(error => {
        
          this.error = error;
        }); 
    },

    // redirect in case of success
    redirect() {
      this.$router.push("/login");
    },


  },
  created() {

  },
};
</script>
